import React, {
  PropTypes
} from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';

/**
 * TransitionShow 
 * @param {object} props 
 * @param {boolean} props.show=false children
 * @param {string} props.transitionName  className
 * @param {number} props.duration 
 * @param {PropTypes.node} props.children 
 * @return {PropTypes.element} React element
 *
 * @example
 * function Dialog(props) {
 *   let {show, duration} = props;
 *
 *   return (
 *     <TranstionShow show={show} transitionName='dialog'>
 *       <div></div>
 *     </TransitionShow>
 *   );
 * }
 */
export default function TransitionShow(props) {
  let {show, transitionName, duration, children} = props;

  return (
    <ReactCSSTransitionGroup
      component={EmptyContainer}
      transitionName={transitionName}
      transitionEnterTimeout={duration}
      transitionLeaveTimeout={duration}>
      {show ? children : null}
    </ReactCSSTransitionGroup>
  );
}

TransitionShow.propTypes = {
  show: PropTypes.bool,
  transitionName: PropTypes.string.isRequired,
  duration: PropTypes.number.isRequired,
  children: PropTypes.node
};

TransitionShow.defaultProps = {
  show: false
};



/**
 * TransitionShow 
 * @type {object}
 * @property {boolean} show true false
 * @property {number} duration ms
 * @property {number} zIndex  z-index 
 * @property {string} timingFunction CSS3 
 * @property {object} style 
 * @property {string} className  class
 */
export const sharePropTypes = TransitionShow.sharePropTypes = {
  show: TransitionShow.propTypes.show,
  duration: TransitionShow.propTypes.duration,
  zIndex: PropTypes.number,
  timingFunction: PropTypes.string,
  style: PropTypes.object,  //  style  duration/zIndex/timgFunction
  className: PropTypes.string  //  class
};

/**
 * TransitionShow  
 * @type {Object}
 */
export const shareDefaultProps = TransitionShow.shareDefaultProps = {
  duration: 80,
  zIndex: 9999,
  timingFunction: 'ease',
  style: {}
};




function EmptyContainer(props) {
  return props.children[0] || null;
}



